import { Component, OnInit } from '@angular/core';
import { Department } from '../entity';
import {UniversityService} from '../university.service';

@Component({
  templateUrl: './department.component.html',
  styleUrls: ['./department.component.css']
})
export class DepartmentComponent implements OnInit {

  departments: Department[];
  constructor(private departmentService: UniversityService) { }

  ngOnInit() {
    this.getDepantments();
  }
  getDepantments(): void {
    this.departmentService.getDepartments ()
      .subscribe(res => this.departments = res);
  }
  delete(department: Department | number): void {
    this.departments = this.departments.filter(h => h !== department);
    this.departmentService.deleteDepartment(department).subscribe(res => alert(res.message));
  }
}
