import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { Title } from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-update-title',
  templateUrl: './update-title.component.html',
  styleUrls: ['./update-title.component.css']
})
export class UpdateTitleComponent implements OnInit {
  @Input() title: Title;

  constructor(
    private route: ActivatedRoute,
    private titleService: UniversityService,
    private location: Location
  ) {
  }

  ngOnInit() {
    const id = this.route.snapshot.params.id;
    this.titleService.getTitle(id)
      .subscribe(res => this.title = res);
  }

  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.titleService.updateTitle(this.title)
      .subscribe(() => this.goBack());
  }
}
