import { Component, OnInit } from '@angular/core';
import { Title } from '../entity';
import {UniversityService} from '../university.service';

@Component({
  selector: 'app-title',
  templateUrl: './title.component.html',
  styleUrls: ['./title.component.css']
})
export class TitleComponent implements OnInit {
  titles: Title[];
  constructor(private titleService: UniversityService) { }

  ngOnInit() {
    this.getTitles();
  }
  getTitles(): void {
    this.titleService.getTitles ()
      .subscribe(res => this.titles = res);
  }
  delete(title: Title | number): void {
    this.titles = this.titles.filter(h => h !== title);
    this.titleService.deleteTitle(title).subscribe(res => alert(res.message));
  }
}
